body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 280px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}


:root {
  --timing: 0.08s;
  --tagoff: #15528A;
  --tagon: #1496BA;
}

.tag {
  display: inline-block;
  position: relative;
  margin: 0 10px 0 0;
}

.tag-filter input[type=checkbox] {
  opacity: 0;
  position: absolute;
  z-index: 300;
  cursor: pointer;
  left: 0;
  width: 100%;
  height: 100%;
}

.tag-filter input[type=checkbox]:hover + label {
  background: #27649C;
}

.tag label {
  z-index: 200;
  color: white;
  display: block;
  background: #15528A;
  background: var(--tagoff);
  text-transform: uppercase;
}

.tag-plain label {
  font-size: 12px;
  padding: 7px;
  
}

.tag-filter label{
  font-size: 16px;
  padding: 7px 34px 7px 18px;
  position: relative;
  border-radius: 16px;
  -webkit-transition: all 0.08s ease-in;
  transition: all 0.08s ease-in;
  -webkit-transition: all var(--timing) ease-in;
  transition: all var(--timing) ease-in;
}

.tag .fa {
  position: absolute;
  z-index: 250;
  right: 15px;
  top: 9px;
  color: white;
}

.tag .fa-check {
  display: none;
  right: 12px;
  top: 9px;
  -webkit-transition: all 0.08s ease-in;
  transition: all 0.08s ease-in;
  -webkit-transition: all var(--timing) ease-in;
  transition: all var(--timing) ease-in;
}


.tag input[type=checkbox]:checked + label {
  background: #1496BA;
  background: var(--tagon);
  -webkit-animation: toggle 0.08s;
          animation: toggle 0.08s;
  -webkit-animation: toggle var(--timing);
          animation: toggle var(--timing);
}

.tag input[type=checkbox]:checked ~ .fa-plus {
  display: none;
}

.tag input[type=checkbox]:checked ~ .fa-check {
  -webkit-animation: toggle 0.08s;
          animation: toggle 0.08s;
  -webkit-animation: toggle var(--timing);
          animation: toggle var(--timing);
  display: block;
}

@-webkit-keyframes toggle {
  0%   {}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}

@keyframes toggle {
  0%   {}
  100% {-webkit-transform: scale(1);transform: scale(1);}
}


h1 {
  font-family: 'Oswald', sans-serif
}
  
body {
  font-family: 'Lato', sans-serif
}

